<template>
    <v-dialog v-model="dialog" scrollable max-width="600px" hide-overlay>
        <template v-slot:activator="{ on }">
            <v-btn color="secondary" class="text-lowercase mr-4" dark small v-on="on">add loyalty</v-btn>
        </template>
        <v-card v-if="!loading">
            <v-card-title class="subtitle-2 pt-6 text-pre-wrap">
                Add all loyalty by <span class="secondary--text">{{ set.item }}</span> set
            </v-card-title>
            <v-divider></v-divider>
            <!-- <v-card-text class="black--text">
                <v-container grid-list-md>
                    <v-form v-model="valid" ref="form" validation>
                        <v-row>
                            <v-col cols="12" md="8" sm="6">
                                <v-select dense return-object 
                                    v-model="segment" 
                                    :items="segments"
                                    item-text="segment.name"
                                    item-value="segmentId"
                                    color="secondary" 
                                    item-color="secondary" label="segment"
                                    @change="onceMore()" 
                                    :rules="[rules.required]">
                                </v-select>
                            </v-col>
                            <v-col cols="12" md="8" sm="6">
                                <v-select dense return-object v-model="trademark" :items="filteredTrademarks"
                                    item-text="name" color="secondary" item-color="secondary" label="trademark"
                                    @change="onceMore()" :rules="[rules.required]">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" label="loyalty" v-model.number="loyalty"
                                    name="loyalty" type="text" :rules="[rules.number]" @change="onceMore()">
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-form>
                </v-container>
                <v-row justify="center" class="error--text">{{ error }}</v-row>
                <v-row justify="center" class="text-pre-wrap subtitle-2">
                    {{ message }}
                </v-row>
            </v-card-text> -->
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" text class="text-capitalize" :disabled="Boolean(error)"
                    @click="addAll">Save
                </v-btn>

                <v-btn color="secondary" text class="text-capitalize" @click="closeDialog">
                    Close
                </v-btn>

            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>

export default {
    name: 'AddLoyalty',
    props: ['set'],
    data() {
        return {
            dialog: false,
            valid: false,
            error: '',
            message: '',
            rules: {
                required: v => !!v || 'this field is required',
                number: v => /^[+]?[0-9]/.test(v) || 'it\'s expected number',
            },
            //props
            loyalty: 0,
            trademark: null,
            segment: null,
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        locale() {
            return this.$store.getters.locale;
        },
        segments() {
            return this.$store.getters.market_segments;
        },
        trademarks() {
            return this.$store.getters.trademarks;
        },
        products() {
            return this.$store.getters.products_all;
        },
        filteredTrademarks() {
            if (this.products?.length > 0) {
                const trs = [];
                this.products?.forEach(prod => {
                    const inArr = trs.find(tr => prod.trademarkId === tr.id)
                    if (!inArr) {
                        trs.push(prod.trademark)
                    }
                })
                return trs
            }
            return this.trademarks
        }
    },
    watch: {
        async set() {
            //console.log('set: ', this.set)
            const params = {
                gameId: this.set.value,
                round: 0
            }
            try {
                await this.$store.dispatch('fetchProducts', params);
                await this.$store.dispatch('fetchMarketSegments', params);
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
    },
    methods: {
        onceMore() {
            this.error = ''
            this.message = '';
        },
        addAll() {
            const resArr = []
            this.segments.forEach((segment) => {
                this.filteredTrademarks.forEach((trad) => {
                    const obj = {
                        gameId: this.set.value,
                        round: 0,
                        trademarkId: trad.id,
                        loyalty_prev: 0,
                        loyalty: 0,
                        trademark: trad,
                        segmentId: segment.segmentId,
                        segment: { name: segment.segment.name, id: segment.segmentId }
                    }
                    this.addLoyalty(obj)
                    resArr.push(obj)
                })
            })
            console.log(resArr)
            //this.closeDialog()
        },
        async addLoyalty(obj) {

            /* if (this.$refs.form.validate()) { */
                //console.log(this.segment)
                try {
                    //add resource
                    await this.$store.dispatch('addLoyalty', obj)
                }
                catch (error) {
                    this.error = `${error.message}`;
                    this.message = '';
                    this.$store.dispatch('setError', error.message)//throw error
                }
           /*  } */
        },
        closeDialog() {
            //reset
            this.dialog = false;
            this.error = '';
            this.message = '';
            //this.$refs.form.reset();
        }
    },
    async mounted() {
        const params = {
            gameId: this.set.value,
            round: 0
        }
        if (this.segments?.length < 1) {
            try {
                await this.$store.dispatch('fetchMarketSegments', params);
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
        if (this.trademarks?.length < 1) {
            try {
                await this.$store.dispatch('fetchTrademarks');
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
        if (this.products?.length < 1) {
            
            try {
                await this.$store.dispatch('fetchProducts', params);
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
    }
}
</script>