<template>
    <v-container fluid>
        <div v-if="!loading">
            <v-row class="my-2 subtitle-2 secondary--text pl-12">
                Brand awareness 
            </v-row>
            <!--table head-->
            <v-divider />
            <v-row class="subtitle-2" no-gutters>
                <v-col v-for="(item, i) in headers" :key="i" class="tabCell" :class="item.style">
                    {{ item.title }}
                </v-col>
            </v-row>
            <div v-if="awareness.length > 0">
                <!--media-costs tab-->
                <v-row v-for="item in awareness" :key="item.id" no-gutters>
                    <v-col class="tabCell text-center">{{ item.trademark.name }}</v-col>
                    <v-col class="tabCell text-center">{{ item.awareness_prev}}</v-col>
                    <v-col class="tabCell text-center">{{ item.awareness }}</v-col>
                </v-row>
            </div>
            <v-row v-else class="mt-8 px-4" justify="center">
                You haven't awareness yet!
            </v-row>
            <v-row class="mt-8 my-2 subtitle-2 secondary--text pl-12">
                Brand loyalty
            </v-row>
            <!--table head-->
            <v-divider />
            <v-row class="subtitle-2" no-gutters>
                <v-col class="tabCell text-left">
                    segment
                </v-col>
                <v-col v-for="(item, i) in headers" :key="i" class="tabCell" :class="item.style">
                    {{ item.title }}
                </v-col>
            </v-row>
            <div v-if="loyalty.length">
                <!--loyalty tab-->
                <v-row v-for="item in loyalty" :key="item.id" no-gutters>
                    <v-col class="tabCell text-left">{{ item.segment.name }}</v-col>
                    <v-col class="tabCell text-center">{{ item.trademark.name }}</v-col>
                    <v-col class="tabCell text-center">{{ item.loyalty_prev }}</v-col>
                    <v-col class="tabCell text-center">{{ item.loyalty }}</v-col>
                </v-row>
            </div>
            <v-row v-else class="mt-8 px-4" justify="center">
                You haven't brand loyalty yet!
            </v-row>
        </div>
        <div v-else class="text-center">
            <loader></loader>
        </div>
    </v-container>
</template>
<script>
import Loader from '../../../layouts/Loader.vue'
import { localeNumber } from '../../../functions/common';

export default {
    name: 'BrandAwarenessLoyalty',
    data() {
        return {
            headers: [
                { title: 'trademark', style: 'text-center' },
                { title: 'value last round', style: 'text-center' },
                { title: 'value current round', style: 'text-center' },
            ],
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading;
        },
        locale() {
            return this.$store.getters.locale;
        },
        awareness() {
            return this.$store.getters.awareness;
        },
        loyalty() {
            return this.$store.getters.loyalty;
        },
    },
    methods: {
        formatNumber(number) {
            return localeNumber(number, this.locale)
        }
    },
    components: {
        Loader
    }
}
</script>