<template>
    <v-container fluid class="pa-0">
        <!--get info-->
        <v-container class="my-4">
            <v-form v-model="valid" ref="form" validation>
                <v-row>
                    <v-col cols="12" sm="4" md="2">
                        <v-select dense color="secondary" 
                            item-color="secondary"
                            return-object
                            v-model="set" 
                            :items="sets" 
                            item-text="item"
                            item-value="value"
                            label="set for game" 
                            :rules="[rules.required]">
                        </v-select>
                    </v-col>
                    <v-col cols="12" sm="3" md="2" class="mt-4">
                        <v-btn color="secondary" small dark class="text-lowercase"
                            :disabled="!valid || Boolean(error)"
                            @click="load">load
                        </v-btn>
                    </v-col>
                    
                    <v-col v-if="valid && chainRout" cols="12" class="mt-4" sm="3" md="2">
                        <add-chain :set="set"></add-chain>
                    </v-col>
                    <v-col v-else-if="valid && afRout" cols="12" class="mt-4" sm="3" md="2">
                        <add-af :set="set"/>
                    </v-col>
                    <v-col v-else-if="valid && mediaRout" cols="12" class="mt-4" sm="6" md="4">
                        <add-media-costs :set="set" /> 
                        <add-media-coverage :set="set" />
                    </v-col>
                    <v-col v-else-if="valid && brandRout" cols="12" class="mt-4" sm="6" md="4">
                        <add-awareness :set="set" />
                        <add-loyalty :set="set" />
                    </v-col>
                    <v-col v-else cols="12" class="mt-4" sm="3" md="2">
                        <add-segment :set="set"/>
                    </v-col>
                </v-row>
            </v-form>
        </v-container>
        <!--tabs-->
        <v-divider></v-divider>
        <v-tabs v-model="tab" height="35"
            background-color="primary"
            fixed-tabs
            hide-slider
            dark>
            <v-tab :to="link(item)" ripple class="text-capitalize" exact-active-class 
                v-for="item in itemsTab"
                :key="item">
                {{ item }}
            </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
            <v-tab-item value="/admin/market/segments">
                <segments></segments>
            </v-tab-item>
            <v-tab-item value="/admin/market/design">
                <design />
            </v-tab-item>
            <v-tab-item value="/admin/market/materials">
                <materials />
            </v-tab-item>
            <v-tab-item value="/admin/market/values">
                <values />
            </v-tab-item>
            <!-- media costs -->
            <v-tab-item value="/admin/market/media">
                <media />
            </v-tab-item>
            <!-- brand awareness & loyalty -->
            <v-tab-item value="/admin/market/awareness">
                <brand-awareness-loyalty />
            </v-tab-item>
            <!--advanced features-->
            <v-tab-item value="/admin/market/features">
                <advanced-features />
            </v-tab-item>
            <v-tab-item value="/admin/market/chains">
                <chains></chains>
            </v-tab-item>
        </v-tabs-items>
    </v-container>
</template>
<script>
import Segments from './market/Segment.vue'
import Values from './market/Values.vue'
import Design from './market/Design.vue';
import Materials from './market/Materials.vue';
import Media from './market/Media.vue';
import BrandAwarenessLoyalty from './market/BrandAwareness&Loyalty.vue';
import AdvancedFeatures from './market/AdvancedFeatures.vue'
import Chains from './market/Chains.vue'

import AddSegment from '../../components/admin/segments/addSegment.vue';
import AddAf from '../../components/admin/advanced-features/addFeature.vue'
import AddChain from '../../components/admin/chains/addChain.vue'
import addMediaCosts from '../../components/admin/media/addMediaCosts.vue';
import AddMediaCoverage from '../../components/admin/media/AddMediaCoverage.vue';
import AddAwareness from '../../components/admin/brand-awareness&loyalty/AddAwareness.vue';
import AddLoyalty from '../../components/admin/brand-awareness&loyalty/AddLoyalty.vue';

export default {
    data() {
        return {
            itemsTab: ['Segments', 'Design', 'Materials', 'Values', 'Media', 'Brand awareness', 'Advanced features', 'Chains'],
            tab: 0,
            valid: false,
            error: '',
            rules: {
                required: value => !!value || 'required field',
            },
            
            set: { item: 'standart', value: 2 },
        }
    },
    computed: {
        user() {
            return this.$store.getters.user;
        },
        games() {
            return this.$store.getters.games;
        },
        //routes
        chainRout() {
            return /\/market\/chains/.test(this.$route.path)
        },
        afRout() {
            return /\/market\/features/.test(this.$route.path)
        },
        mediaRout() {
            return /\/market\/media/.test(this.$route.path)
        },
        brandRout() {
            return /\/market\/awareness/.test(this.$route.path)
        },
        sets() {
            const fixed_arr = [
                { item: 'simple', value: 1 },
                { item: 'standart', value: 2 },
                { item: 'advanced', value: 3 }
            ]

            const custom_arr = this.games.filter(game => game.id > 3)
            if (custom_arr.length > 0) {
                custom_arr.map(item => {
                    fixed_arr.push({
                        item: item.id,
                        value: item.id
                    })
                })
            }

            return fixed_arr;
        }
    },
    methods: {
        link(name) {
            const arr = name.split(' ');
            if (arr.length > 1) {
                return `/admin/market/${arr[1].toLowerCase()}`
            }
            return `/admin/market/${name.toLowerCase()}`
        },
        async load() {
            let gameId = this.set.value;
            let round = 0;
            if (gameId > 3) {
                const currentGame = this.games.find(game => game.id == gameId);
                if (currentGame) {
                   round = currentGame.round; 
                } 
            }
            const obj = {
                gameId: gameId,
                round: round
            }
            try {
                if (this.chainRout) {
                    await this.$store.dispatch('fetchChains', gameId);
                }
                else if (this.afRout) {
                    await this.$store.dispatch('fetchAdvancedFeatures', obj);
                }
                else if (this.brandRout) {
                    await this.$store.dispatch('fetchAwareness', obj);
                    await this.$store.dispatch('fetchLoyalty', obj);
                }
                else if (this.mediaRout) {
                    await this.$store.dispatch('fetchMediaCosts', obj);
                    await this.$store.dispatch('fetchMediaCoverage', obj);                }
                else {
                   await this.$store.dispatch('fetchMarketSegments', obj); 
                }
            }
            catch (err) {
                console.log(err)
            }
        },
        addLoyalty() {

        }
    },
    components: {
        Segments, Values, Design, Materials, AdvancedFeatures, 
        Chains, Media, BrandAwarenessLoyalty,
        AddSegment, AddChain, AddAf, addMediaCosts, AddMediaCoverage,
        AddAwareness, AddLoyalty
    },
    async mounted() {
        if (!this.games.length) {
            try {
                await this.$store.dispatch('fetchAllGames');
            }
            catch (error) {
                console.log(error.message)
            }
        }
    }

}
</script>
