<template>
    <v-container fluid>
        <div v-if="!loading">
            <v-row class="my-2 subtitle-2 secondary--text pl-12">
                Media costs
            </v-row>
            <!--table head-->
            <v-divider />
            <v-row class="subtitle-2" no-gutters>
                <v-col v-for="(item, i) in headers" :key="i" class="tabCell" :class="item.style">
                    {{ item.title }}
                </v-col>
            </v-row>
            <div v-if="media_costs?.length">
                <!--media-costs tab-->
                <v-row v-for="cost in media_costs" :key="cost.id" no-gutters>
                    <v-col class="tabCell text-center">{{ cost.smm }}</v-col>
                    <v-col class="tabCell text-center">{{ cost.content_ads }}</v-col>
                    <v-col class="tabCell text-center">{{ cost.bloggers_influencers }}</v-col>
                    <v-col class="tabCell text-center">{{ cost.postal }}</v-col>
                    <v-col class="tabCell text-center">{{ formatNumber(cost.outdoor_advertising) }}</v-col>
                    <v-col class="tabCell text-center">{{ formatNumber(cost.autoradio_podcasts) }}</v-col>
                    <v-col class="tabCell text-center">{{ formatNumber(cost.tv) }}</v-col>
                    <v-col class="tabCell text-right">
                        <delete-media-cost :media_costs="cost" />
                    </v-col>
                </v-row>
            </div>
            <v-row v-else class="mt-8 px-4" justify="center">
                You haven't media costs yet!
            </v-row>
            <v-row class="mt-8 my-2 subtitle-2 secondary--text pl-12">
                    Media coverage
                </v-row>
                <!--table head-->
                <v-divider />
                <v-row class="subtitle-2" no-gutters>
                    <v-col class="tabCell text-left">
                            segment
                    </v-col>
                    <v-col v-for="(item, i) in headers" :key="i" class="tabCell" :class="item.style">
                        {{ item.title }}
                    </v-col>
                </v-row>
                <div v-if="media_coverage?.length">
                    <!--media-costs tab-->
                    <v-row v-for="coverage in media_coverage" :key="coverage.id" no-gutters>
                        <v-col class="tabCell text-left">{{ coverage.segment.name }}</v-col>
                        <v-col class="tabCell text-center">{{ coverage.smm }}</v-col>
                        <v-col class="tabCell text-center">{{ coverage.content_ads }}</v-col>
                        <v-col class="tabCell text-center">{{ coverage.bloggers_influencers }}</v-col>
                        <v-col class="tabCell text-center">{{ coverage.postal }}</v-col>
                        <v-col class="tabCell text-center">{{ formatNumber(coverage.outdoor_advertising) }}</v-col>
                        <v-col class="tabCell text-center">{{ formatNumber(coverage.autoradio_podcasts) }}</v-col>
                        <v-col class="tabCell text-center">{{ formatNumber(coverage.tv) }}</v-col>
                        <v-col class="tabCell text-right">
                            <delete-media-coverage :media_coverage="coverage" />
                        </v-col>
                    </v-row>
                </div>
                <v-row v-else class="mt-8 px-4" justify="center">
                    You haven't media coverage yet!
                </v-row>
        </div>   
        <div v-else class="text-center">
            <loader></loader>
        </div>
    </v-container>
</template>
<script>
import Loader from '../../../layouts/Loader.vue'
import { localeNumber } from '../../../functions/common';
import DeleteMediaCost from '../../../components/admin/media/DeleteMediaCost.vue';
import DeleteMediaCoverage from '../../../components/admin/media/DeleteMediaCoverage.vue';

export default {
    name: 'Media',
    data() {
        return {
            headers: [
                { title: 'SMM', style: 'text-center' },
                { title: 'Content Ads', style: 'text-center' },
                { title: 'Bloggers & influencers', style: 'text-center' },
                { title: 'Postal', style: 'text-center' },
                { title: 'Outdoor advertising', style: 'text-center' },
                { title: 'Autoradio podcasts', style: 'text-center' },
                { title: 'TV', style: 'text-center' },
                { title: 'Action', style: 'text-right' },
            ],
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading;
        },
        locale() {
            return this.$store.getters.locale;
        },
        media_costs() {
            return this.$store.getters.media_costs;
        },
        media_coverage() {
            return this.$store.getters.media_coverage;
        },
        segments() {
            return this.$store.getters.market_segments;
        }
    },
    methods: {
        formatNumber(number) {
            return localeNumber(number, this.locale)
        }
    },
    components: {
        Loader, DeleteMediaCost, DeleteMediaCoverage,
    }
}
</script>