<template>
    <v-dialog v-model="dialog" scrollable max-width="600px" hide-overlay>
        <template v-slot:activator="{ on }">
            <v-btn color="secondary" class="text-lowercase mr-4" dark small v-on="on">add media costs</v-btn>
        </template>
        <v-card v-if="!loading">
            <v-card-title class="subtitle-2 pt-6 text-pre-wrap">
                New media costs by <span class="secondary--text">{{ set.item }}</span> set
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text class="black--text">
                <v-container grid-list-md>
                    <v-form v-model="valid" ref="form" validation>
                        <v-row>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="SMM"
                                    persistent-hint
                                    v-model.number="smm" 
                                    name="smm" 
                                    type="text"
                                    :rules="[rules.number]"
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="content ads"
                                    v-model.number="content_ads" 
                                    name="content_ads" 
                                    type="text"
                                    :rules="[rules.number]" 
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="bloggers & influencers" 
                                    v-model.number="bloggers_influencers"
                                    name="bloggers_influencers" 
                                    type="text"
                                    :rules="[rules.number]" 
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="postal mail" 
                                    v-model.number="postal" 
                                    name="postal" 
                                    type="text" 
                                    :rules="[rules.number]"
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="outdoor advertising" 
                                    v-model.number="outdoor_advertising" 
                                    name="outdoor_advertising" 
                                    type="text" 
                                    :rules="[rules.number]"
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="autoradio podcasts" 
                                    v-model.number="autoradio_podcasts" 
                                    name="autoradio_podcasts" 
                                    type="text" 
                                    :rules="[rules.number]"
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="TV" 
                                    v-model.number="tv" 
                                    name="tv" 
                                    type="text" 
                                    :rules="[rules.number]"
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-form>
                </v-container>
                <v-row justify="center" class="error--text">{{ error }}</v-row>
                <v-row justify="center" class="text-pre-wrap subtitle-2">
                    {{ message }}
                </v-row>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" text class="text-capitalize" :disabled="!valid || Boolean(error)"
                    @click="addMediaCosts">Save
                </v-btn>

                <v-btn color="secondary" text class="text-capitalize" @click="closeDialog">
                    Close
                </v-btn>

            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>

export default {
    name: 'AddMediaCosts',
    props: ['set'],
    data() {
        return {
            dialog: false,
            valid: false,
            error: '',
            message: '',
            rules: {
                required: v => !!v || 'this field is required',
                number: v => /^[+]?[0-9]/.test(v) || 'it\'s expected number',
            },
            //props
            smm: 0,
            content_ads: 0,
            bloggers_influencers: 0,
            postal: 0,
            outdoor_advertising: 0,
            autoradio_podcasts: 0,
            tv: 0,
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        locale() {
            return this.$store.getters.locale;
        },
        
    },
    methods: {
        onceMore() {
            this.error = ''
            this.message = '';
        },
        async addMediaCosts() {

            if (this.$refs.form.validate()) {
                
                const media_costs = {
                    gameId: this.set.value,
                    round: 0,
                    smm: this.smm,
                    content_ads: this.content_ads,
                    bloggers_influencers: this.bloggers_influencers,
                    postal: this.postal,
                    outdoor_advertising: this.outdoor_advertising,
                    autoradio_podcasts: this.autoradio_podcasts,
                    tv: this.tv,
                }
                /*console.log(media_costs);*/
                try {
                    //add resource
                    await this.$store.dispatch('addMediaCosts', media_costs)
                    this.closeDialog()
                }
                catch (error) {
                    this.error = `${error.message}`;
                    this.message = '';
                    this.$store.dispatch('setError', error.message)//throw error
                }
            }
        },
        closeDialog() {
            //reset
            this.dialog = false;
            this.error = '';
            this.message = '';
            this.$refs.form.reset();
        }
    },
}
</script>